<template>
  <el-form :model="searchForm" ref="searchForm" label-width="50px">
    <el-row>
      <el-col :span="4">
        <el-form-item label="仓库" prop="warehouseUuid">
          <el-select v-model="searchForm.warehouseUuid" filterable class="w100p" @change="getMerchantsList">
            <el-option
              v-for="(org, index) in warehouseList"
              :key="index"
              :label="org.warehouseName"
              :value="org.warehouseUuid"></el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="4">
        <el-form-item label="商户"  prop="merchantUuid">
          <el-select v-model="searchForm.merchantUuid" filterable clearable placeholder="请选择商户" @change="getBrandList" class="w100p">
            <el-option
              v-for="(item, index) in merchantsList"
              :key="index"
              :label="item.userName"
              :value="item.userUuid"
            />
          </el-select>
        </el-form-item>
      </el-col>
      <!--<el-col :span="4">-->
        <!--<el-form-item label="品牌" prop="productBrand">-->
          <!--<el-select clearable placeholder="请选择品牌" filterable clearable v-model="searchForm.productBrand" >-->
            <!--<el-option-->
              <!--v-for="(item, index) in brandList"-->
              <!--:key="index"-->
              <!--:label="item"-->
              <!--:value="item"-->
            <!--&gt;</el-option>-->
          <!--</el-select>-->
        <!--</el-form-item>-->
      <!--</el-col>-->
      <el-col :span="5">
        <el-form-item label="出库单号" label-width="80px" prop="deliverOrderId">
          <el-input v-model="searchForm.deliverOrderId" placeholder="请输入出库单号"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="4">
        <el-form-item label="状态" prop="orderStatus">
          <el-select v-model="searchForm.orderStatus" clearable class="w100p">
            <!--<el-option class="radio" value="0" label="待提交"></el-option>-->
            <el-option class="radio" value="1" label="待审核"></el-option>
            <el-option class="radio" value="2" label="审核通过"></el-option>
            <el-option class="radio" value="3" label="审核未通过"></el-option>
            <el-option class="radio" value="4" label="待发货"></el-option>
            <el-option class="radio" value="5" label="已发货"></el-option>
          </el-select>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row type="flex" justify="start">
      <el-col :span="6">
        <el-form-item label="送货日期(起)" label-width="100px" prop="sendBeginDate">
          <el-date-picker
            class="w100p"
            v-model="searchForm.sendBeginDate"
            type="date"
            align="right"
            clearable
            unlink-panels
            placeholder="请选择送货日期(起)"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd"
            size="mini"
          >
          </el-date-picker>
        </el-form-item>

      </el-col>
      <el-col :span="6">
        <el-form-item label="送货日期(止)" label-width="100px" prop="sendEndDate">
          <el-date-picker
            class="w100p"
            v-model="searchForm.sendEndDate"
            type="date"
            align="right"
            clearable
            unlink-panels
            placeholder="请选择送货日期(止)"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd"
            size="mini"
          >
          </el-date-picker>
        </el-form-item>
      </el-col>
      <!--<el-col :span="4">-->
        <!--<el-form-item label="客户姓名"  prop="customerName" label-width="80px">-->
          <!--<el-input v-model="searchForm.customerName" maxlength="200" clearable  ></el-input>-->
        <!--</el-form-item>-->
      <!--</el-col>-->
      <!--<el-col :span="5">-->
        <!--<el-form-item label="客户电话"  prop="customerTelephone" label-width="80px">-->
          <!--<el-input v-model="searchForm.customerTelephone"  maxlength="200"  clearable></el-input>-->
        <!--</el-form-item>-->
      <!--</el-col>-->
      <!--<el-col :span="8">-->
        <!--<el-form-item label="客户地址" prop="customerAddress" label-width="80px">-->
          <!--<el-input  clearable v-model="searchForm.customerAddress" type="textarea" :autosize="{maxRows:3}" maxlength="200" resize="none"></el-input>-->
        <!--</el-form-item>-->
      <!--</el-col>-->
      <el-col :span="4" :offset="2" class="flex-right">
        <el-button type="primary" @click.native="search" style="height: 30px;">查询</el-button>
        <el-button @click="resetForm('searchForm')" style="height: 30px;">重置</el-button>
      </el-col>
    </el-row>
  </el-form>
</template>

<script>
  import moment from 'moment'
  import { getWarehouseList } from '@/api/warehouse'
  import { getAllMerchantsList } from '@/api/common'
  import { setSession, getSession } from '@/utils/session'
  export default {
    name: 'DeliveryManagementSearch',
    data () {
      return {
        // 检索界面数据
        searchForm: {
          warehouseUuid:'',
          merchantUuid:'',
          productBrand:'',
          deliverOrderId:'',
          orderStatus:'',
          customerName:'',
          customerTelephone:'',
          customerAddress:'',
          createBeginDate:'',
          createEndDate:'',
          sendBeginDate:'',
          sendEndDate:'',
        }
      }
    },
    props: {
      warehouseList: {
        type: Array
      },
      merchantsList: {
        type: Array
      },
      brandList: {
        type: Array
      }
    },
    activated () {
      this.searchForm= {
        warehouseUuid:'',
        merchantUuid:'',
        productBrand:'',
        deliverOrderId:'',
        orderStatus:'',
        createTimeRange:'',
        goodsDeliverDate:'',
        customerName:'',
        customerTelephone:'',
        customerAddress:'',
        createBeginDate:'',
        createEndDate:'',
        sendBeginDate:'',
        sendEndDate:'',
      }
      this.searchForm.warehouseUuid = getSession('warehouseUuid')
      this.search()
      this.getMerchantsList(this.searchForm.warehouseUuid)
    },
    mounted () {

    },
    methods: {
      getMerchantsList (value) {
        setSession('warehouseUuid',value)
        this.searchForm.goodsDeliverMerchantUuid = ''
        this.searchForm.productBrand = ''
        this.searchForm.merchantUuid = ''
        this.$emit('getMerchants',Object.assign({},this.searchForm))
        this.$emit('getBrands',Object.assign({},this.searchForm))
        this.search()
      },
      getBrandList () {
        this.searchForm.productBrand = ''
        this.$emit('getBrandModel',{warehouseUuid:getSession('warehouseUuid'),merchantUuid:this.searchForm.goodsDeliverMerchantUuid})
      },
      // 检索
      search () {
        if (this.searchForm.sendEndDate && this.searchForm.sendBeginDate && ( moment(this.searchForm.sendEndDate).isBefore(this.searchForm.sendBeginDate) || moment(this.searchForm.sendEndDate).isSame(this.searchForm.sendBeginDate))) {
        this.$message({
          message: '送货日期(止)不能早于送货日期(起)，请重新选择！',
          type: 'warning'
        })
        this.searchForm.sendEndDate = null
      } else {
        this.$emit('search', Object.assign({}, this.searchForm))
      }
      },
      // 重置
      resetForm (formName) {
        this.$refs[formName].resetFields()
        this.searchForm.warehouseUuid = getSession('warehouseUuid')
        this.$emit('rest', Object.assign({}, this.searchForm))
      }
    }
  }
</script>

<style lang="scss" scoped>
  .w100p{
    width: 100%;
  }
  .el-form-item{
    margin-bottom:10px;
  }
  .mar-b-10{
    margin-bottom: 10px;
  }
</style>



