<template>
  <div>
    <!-- 查询 -->
    <common-wrapper>
      <delivery-management-search
        class="search"
        :warehouseList="warehouseList"
        :merchantsList="searchMerchantsList"
        :modelBrand="modelBrand"
        :brandList="brandList"
        :allModelList="modelList"
        @getMerchants="getSearchMerchantsList"
        @getBrandModel="getBrandModel"
        @search="queryList"
        @rest="rest" />
    </common-wrapper>
    <nav-button>
      <el-button-group>
        <el-button icon="fdddfont icon-add-new" @click="handleAdd">新增</el-button>
        <el-button icon="el-icon-delete" @click="handleDel" :disabled="isButtonIsDisabled">删除</el-button>
        <el-alert
          style="display: inline;height: 29px;line-height: 29px;margin-left: 10px;"
          v-if="flag==1"
          :closable="false"
          title="选中项包含已发货的出库单，请重新选择！"
          type="warning"
          show-icon>
        </el-alert>
        <el-alert
          style="display: inline;height: 29px;line-height: 29px;margin-left: 10px;"
          v-if="flag2==1"
          :closable="false"
          title="选中项包含审核通过的出库单，请重新选择！"
          type="warning"
          show-icon>
        </el-alert>
        <!--<el-button icon="el-icon-edit-outline" @click="check" :disabled="!isButtonIsDisabled()">审核</el-button>-->
      </el-button-group>
    </nav-button>
    <!-- 订单列表 -->
    <common-wrapper>
      <common-table :flex="270">
        <template slot="table">
          <el-table
            ref="orderTable"
            height="100%"
            stripe
            :border="true"
            tooltip-effect="light"
            v-loading="isListLoading"
            :data="tableData"
            @row-dblclick="getOrderDetail"
            @selection-change="selected => selectRow = selected">
            <el-table-column type="selection" width="30" fixed="left"></el-table-column>
            <el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column>
            <el-table-column prop="goodsDeliverUuid" sortable label="出库单号" width="150"></el-table-column>
            <el-table-column prop="goodsDeliverMerchantUuid" sortable label="商户" min-width="120" :formatter="formatMerchant"></el-table-column>
            <el-table-column prop="customerName" sortable label="客户姓名" min-width="100"></el-table-column>
            <el-table-column prop="customerTelephone" sortable label="客户电话" width="120"></el-table-column>
            <el-table-column prop="customerAddress" label="客户地址" width="120" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="goodsDeliverDate" sortable label="送货日期" width="120" ></el-table-column>
            <el-table-column prop="customerNeedInstall" label="是否安装" width="70" :formatter="formatEnable"></el-table-column>
            <el-table-column prop="customerLiftFlag" label="是否有电梯" width="80" :formatter="formatEnable"></el-table-column>
            <!--<el-table-column prop="customerServicePhone" sortable label="售后电话" width="120"></el-table-column>-->
            <el-table-column prop="goodsWarehouseUuid" sortable label="仓库" :formatter="formatWarehouse"></el-table-column>
            <el-table-column prop="goodsDeliverStatus" label="状态" :formatter="formatState"></el-table-column>
            <el-table-column prop="goodsDeliverRemark" label="备注" width="120" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column label="操作" width="200" fixed="right">
              <template slot-scope="scope">
                <el-tooltip class="item" effect="light" content="查看" placement="bottom" v-if="showScan(scope.row)">
                  <el-button size="mini" round icon="el-icon-view" @click.stop="handleScan(scope.row)"></el-button>
                </el-tooltip>
                <el-tooltip class="item" effect="light" content="编辑" placement="bottom"  v-if="showEdit(scope.row)">
                  <el-button size="mini" round icon="fdddfont icon-modify"  @click.stop="handleEdit(scope.row)"></el-button>
                </el-tooltip>
                <el-tooltip class="item" effect="light" :content="scope.row.goodsDeliverStatus == 1 ? '审核' :'提交审核'"  v-if="showCheck(scope.row)" placement="bottom">
                  <el-button size="mini" round icon="el-icon-tickets" @click.stop="check(scope.row)"></el-button>
                </el-tooltip>
                <el-tooltip class="item" effect="light" content="取消审核" placement="bottom" v-if="scope.row.goodsDeliverStatus == '2'">
                  <el-button size="mini" round icon="el-icon-circle-close-outline" @click.stop="cancelOrder(scope.row)"></el-button>
                </el-tooltip>
              </template>
            </el-table-column>
          </el-table>
        </template>
        <template slot="pagination">
          <el-pagination   background   layout="total, prev, pager, next"
                           :current-page.sync="pagination.pageNum"
                           :page-size="pagination.pageSize"
                           :total="pagination.total"
                           @current-change="changePage"
          ></el-pagination>
        </template>
      </common-table>
    </common-wrapper>
    <!-- 新增订单-->
    <order-create
        :existOrder="existOrder"
      :show.sync="isCreateFormVisible"
      :openType="openType"
      :selectedOrderInfo="selectedOrderInfo"
      :warehouseList="warehouseList"
      :merchantsList="merchantsList"
      :searchParams="searchParams"
      @search="queryList"
      @rest="rest"
    />
    <!-- 查看订单-->
    <order-detail
      :show.sync="isDetailFormVisible"
      :selectedOrderInfo="selectedOrderInfo"
      :warehouseList="warehouseList"
      :merchantsList="merchantsList"
      @updateCount="updateCount"
    />
    <ensure-check
      :show.sync="isCheckFormVisible"
      :searchParams="searchParams"
      :checkInfo="checkInfo"
      @updateTable="updateTable"
    />
  </div>
</template>

<script>
  import DeliveryManagementSearch from './Components/Search'
  import CommonWrapper from '@/components/commonWrapper'
  import CommonTable from '@/components/commonTable'
  import NavButton from '@/components/navButton'
  import OrderCreate from './Components/OrderCreate'
  import OrderDetail from './Components/OrderDetail'
  import EnsureCheck from './Components/EnsureCheck'
  import { setSession, getSession } from '@/utils/session'
  import { getOrgTree } from '@/api/organization'
  import { getDeliveryOrder, checkDeliveryOrder, deleteDeliveryOrder, ensureCheckDelivery,cancelDeliver } from '@/api/order'
  import { getWarehouseList } from '@/api/warehouse'
  import { getAllMerchantsList,getBrandModel } from '@/api/common'
  import {mixins} from '@/mixins'
  import moment from 'moment'

  export default {
  name: 'Delivery',
    mixins: [mixins],
    data () {
    return {
      warehouseList: this.$store.getters.warehouseList,
      merchantsList: [],
      searchMerchantsList:[],
      brandList: [],
      modelBrand: [],
      modelList: [],
      tableData: [],
      selectRow: [], // 列表选中列
      selectedOrderInfo: {},
      isListLoading: false, // 列表加载
      pagination: { // 翻页数据
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      searchParams: {}, // 查询参数
      isCreateFormVisible: false,
      openType: 1,
      isDetailFormVisible:false,
      isCheckFormVisible: false,
      checkInfo: {},
      flag: 2,
      flag2: 2,
        existOrder:[]
    }
  },
  mounted () {
    let params = {warehouseUuid:getSession('warehouseUuid')}
        this.getMerchantsList(params)
        this.getSearchMerchantsList(params)
        this.getBrandModel(params)
  },
    computed:{
      isButtonIsDisabled () {
        this.flag = 2
        this.flag2 = 2
        if(this.selectRow.length){
          try {
            this.selectRow.forEach(item => {
              if (item.goodsDeliverStatus == '5') {
                this.flag = 1
              } else {
                this.flag = 0
              }
              if (item.goodsDeliverStatus == '2'){
                this.flag2 = 1
              } else {
                this.flag2 = 0
              }
            })
          }catch (e) {
          }
        }else{
          this.flag = 2
          this.flag2 = 2
        }
        if(this.flag == 0 && this.flag2 == 0){
          return false
        }else{
          return true
        }
      },
    },
  methods: {
    // 查询和初始化查询
    queryList (params) {
      const searchParams = this.searchParams = params
      this.getMerchantsList({warehouseUuid:searchParams.warehouseUuid})
      this.getOrders(
        {
          ...searchParams,
          pageNum: this.pagination.pageNum,
          pageSize: this.pagination.pageSize
        }
      )
    },
    rest (params) {
      const searchParams = this.searchParams = params
      this.getOrders(
        {
          ...searchParams,
          pageNum: this.pagination.pageNum,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 列表翻页
    changePage (pageNum) {
      this.pagination.pageNum = pageNum
      this.queryList(this.searchParams)
    },
    // 生成列表序号
    indexMethod (index) {
      return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
    },
    // 处理订单状态
    formatState (row,col) {
      switch(row.goodsDeliverStatus){
        /*case '0':
          return '待提交';
          break;*/
        case '1':
          return '待审核';
          break;
        case '2':
          return '审核通过';
          break;
        case '3':
          return '审核未通过';
          break;
        case '4':
          return '待发货';
          break;
        case '5':
          return '已发货';
          break;
        default:
          return '未知';
      }
    },
    formatEnable (row,col) {
      let choose
      if (col.property === 'customerNeedInstall') {
        choose = row.customerNeedInstall
      } else if (col.property === 'customerLiftFlag') {
        choose = row.customerLiftFlag
      }
      switch(choose){
        case '0':
          return '是';
          break;
        case '1':
          return '否';
          break;
        default:
          return '未知';
      }
    },
    formatWarehouse (row) {
      let result = this.warehouseList.find(item => { return item.warehouseUuid === row.goodsWarehouseUuid})
      if(result){
        return result.warehouseName
      }
    },
    formatMerchant (row) {
      let merchant = this.merchantsList.find(item => { return item.userUuid === row.goodsDeliverMerchantUuid})
      if (merchant) {
        return merchant.userName
      } else {
        return '---'
      }
    },
    // 获取订单列表
    getOrders (params) {
      console.log(params)
      this.isListLoading = true
      getDeliveryOrder(params).then(res => {
        this.isListLoading = false
        if(res.item.length == 0 && res.pageNum > 1){
          this.pagination.pageNum = 1
          params.pageNum = 1
          this.getOrders (params)
          return
        }
        this.tableData = res.item
        for(let order of this.tableData){
          order.goodsDeliverDate = moment(order.goodsDeliverDate).utcOffset(8).format('YYYY-MM-DD')
        }
        this.pagination.total = res.total
        this.pagination.pageNum = res.pageNum
        this.pagination.pageSize = res.pageSize
      })
    },
    // 列表翻页
    changePage (pageNum) {
      this.pagination.pageNum = pageNum
      this.queryList(this.searchParams)
    },
    // 生成列表序号
    indexMethod (index) {
      return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
    },
    updateTable () {
      this.queryList(this.searchParams)
    },
    // 商户列表
    getMerchantsList (params) {
      return new Promise(resolve => {
        getAllMerchantsList(params).then(res => {
          if(!res.error) {
            this.merchantsList = res
            resolve()
          }
        })
      })
    },
    getSearchMerchantsList(params){
      getAllMerchantsList(params).then(res => {
        this.searchMerchantsList = res
      })
    },
    // 获取品牌型号列表
    getBrandModel (params) {
      getBrandModel(params).then(res => {
        if (!res.error) {
          this.brandList = []
          this.modelList = []
          res.forEach((item,index) => {
            this.brandList.push(item.productBrand)
            item.productModel.forEach(item2 => {
              this.modelList.push(item2)
            })
          })
          this.modelBrand = res
        }
      })
    },
    // 订单详情
    getOrderDetail (row, event) {

    },
    handleAdd () {
        this.existOrder = this.tableData.filter(item => {
            return (item.goodsDeliverStatus == '1' || item.goodsDeliverStatus == '3')
        })
        // if(result){
        //     this.$alert('当前存在未审核通过的出库单，请审核通过后再新增出库单', '提示', {
        //         confirmButtonText: '确定'
        //     });
        // }else{
            this.isCreateFormVisible = true
            this.openType = 1
        // }

    },
    handleDel (data) {
      this.$confirm('确认删除该出库单吗?', '提示', {
        type: 'warning'
      }).then(() => {
        let params = {
          orderAccount: []
        }
        for(let item of this.selectRow){
          params.orderAccount.push(item.goodsDeliverUuid)
        }
        deleteDeliveryOrder(params).then(res => {
          if(!res.error){
            this.$message({
              message: '删除成功',
              type: 'success'
            })
            this.queryList(this.searchParams)
          }
        })
      }).catch(() => {})
    },
    handleScan (data) {
      this.isDetailFormVisible = true
      this.selectedOrderInfo = data
    },
    handleEdit (data) {
      this.isCreateFormVisible = true
      this.openType = 2
      this.selectedOrderInfo = data
    },
    showScan (row) {
      if (row.goodsDeliverStatus == '1' || row.goodsDeliverStatus == '3') {
        return false
      } else {
        return true
      }
    },
    showEdit(row) {
      if (row.goodsDeliverStatus == '2' || row.goodsDeliverStatus == '4' || row.goodsDeliverStatus == '5') {
        return false
      } else {
        return true
      }
    },
    showCheck (row) {
      if (row.goodsDeliverStatus == '1' || row.goodsDeliverStatus == '3') {
        return true
      } else {
        return false
      }
    },
    cancelOrder(row){
      this.$confirm('确认取消该出库单吗?', '提示', {
        type: 'warning'
      }).then(() => {
        let params = {deliverOrderId:row.goodsDeliverUuid}
        cancelDeliver(params).then(res => {
          if(!res.error){
            this.$message({
              message: '取消出库单成功',
              type: 'success'
            })
            this.queryList(this.searchParams)
          }
        })
      }).catch(() => {})
    },
    // 审核
    check (row) {
      if (row.goodsDeliverStatus == '3' || row.goodsDeliverStatus == '1') {
        this.isCheckFormVisible = true
        this.checkInfo = row
      }
    },
    // 补打条码更新货物数量
    updateCount() {
      this.queryList(this.searchParams)
    }
  },
  components: {
    DeliveryManagementSearch,
    CommonWrapper,
    CommonTable,
    OrderCreate,
    OrderDetail,
    NavButton,
    EnsureCheck
  }
}
</script>

<style scoped>

</style>



